import { mapMutations, mapState, mapActions } from 'vuex';
const token = uni.getStorageSync('TOKEN');
export default {
	data() {
		return {
			info: null
		};
	},
	computed: {
		...mapState(['isLogin'])
	},
	onLoad() {
	},
	onShow() {
		if (token) {
			this.getUserInfo();
		}
	},
	methods: {
		...mapActions(['navFilter']),
		getUserInfo() {
			this.$Fetch({
				url: 'user/getDataById',
				success: res => {
					console.log(res);
					this.info = res;
				}
			});
		}
	}
};
